.root {
  display: flex;
  justify-content: center;
  position: relative

}

.descriptionContainer {
  width: 30%;
  padding: 20px;
}

.imageContainer {
  width: 70%;
  max-width: 500px
}

.imageContainer > * {
  width: 100%
}

.detailsList {
  list-style:none;
  padding: 0;
  margin: 0;
  font-size: 20px;
}

.listItem {
  margin-top: 20px
}

.listItem > * {
  color: #FFF;
  font-size: 20px;
  text-decoration: none;
}